<template>
  <div class="box">
        <div class="msg_heard">
            <div @click="back">
                <van-icon name="arrow-left" color="#fff"  />
            </div>
            <div><p class="p">个人信息</p></div>
            <div></div>
        </div>
        <van-cell title="头像" is-link value="">
            <van-uploader :after-read="afterRead">
             <van-image
                round
                width="40px"
                height="40px"
                :src="headImg"
            />
            </van-uploader>
           
        </van-cell>
        <van-cell title="昵称" is-link :value="valuename" @click="showPopup"/>
           <van-popup v-model="show">
             <div class='chengename'>
                <p>请输入昵称:</p>
                <input type="text" v-model="value"/>
                <div class="btn">
                    <van-button type="primary" @click="queren">保存</van-button>
                    <van-button type="default" @click="quxiao">取消</van-button>
                </div>               
             </div>
           </van-popup>
           <!-- 性别 -->
        
        <van-cell title="性别" is-link :value="valuesex" @click="showPopup1"/>
            <van-popup v-model="show1">
                <div class='chengename'>
                    <p>请选择性别:</p>
                    <van-field name="radio">
                        <template #input>
                            <van-radio-group v-model="radio" direction="horizontal">
                                <van-radio name="男">男</van-radio>
                                <van-radio name="女">女</van-radio>
                            </van-radio-group>
                        </template>
                    </van-field>
                    <div class="btn">
                        <van-button type="primary" @click="queren1">保存</van-button>
                        <van-button type="default" @click="quxiao">取消</van-button>
                    </div>               
                </div>
            </van-popup>
        <van-cell title="生日" is-link :value="time" @click="showPopup2"/>
            <van-popup v-model="show2">
                <van-datetime-picker v-model="currentDate" type="date" title="选择年月日" 
                class="shijian"
                :min-date="minDate" 
                :max-date="maxDate"
                @confirm="queren2"
                @cancel="quxiao"
                 />
            </van-popup>
        <div class="btn">
            <van-button type="primary"  block  @click="userbaocun" >保存</van-button>
        </div>
  </div>
</template>

<script>
import { Cell, CellGroup ,Icon,Button,Popup,Field,Form,RadioGroup, Radio,DatetimePicker,Toast,Image as VanImage,Uploader} from 'vant';
import {nanoid} from 'nanoid';
export default {
    name:'mymsg',
   data() {
    return {
        value:'',
        show: false,
        show1:false,
        show2:false,
        valuename:'',
        valuesex:'',
        radio: '',
        minDate: new Date(1920, 0, 1),
        maxDate: new Date(),
        currentDate: new Date(2021, 0, 17),
        time:'',
        userval:'',
        fileList:[],
        headImg:'',
        files:null
    }
   },
   components:{
    [Cell.name]:Cell,
    [CellGroup.name]:CellGroup,
    [Icon.name]:Icon,
    [Button.name]:Button ,
    [Popup.name]:Popup,
    [Field.name]:Field, 
    [Form.name]:Form,
    [RadioGroup.name]:RadioGroup,
    [Radio.name]:Radio,
    [DatetimePicker.name]:DatetimePicker,
    [Toast.name]:Toast,
    [VanImage.name]:VanImage,
    [Uploader.name]:Uploader   
   },
   methods:{
    back(){
        this.$router.go(-1);
    },
    showPopup() {
      this.show = true;
    },
    showPopup1() {
      this.show1 = true;
    },
    showPopup2() {
      this.show2 = true;
    },
    queren(){
        this.valuename= this.value;
        
        this.show = false;
        
        this.value=''
    },
    queren1(){
        this.valuesex=this.radio
        this.show1 = false;
    },
    queren2(){
        this.time=this.currentDate.toLocaleDateString();
        // console.log(this.currentDate.toLocaleDateString());
        this.show2= false;
    },
    quxiao(){
        this.show = false;
        this.value=''
        this.show1 = false;
        this.show2 = false;
    },
    chengeuser(){
        this.$axios({
            method:'get',
            url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token'

        }).then(res=>{
            // console.log(this.files);
            let FD =new FormData();
             FD.append('key',nanoid());
             FD.append('file',this.files);
             FD.append('token',res.data.token);
             this.$axios({
                method:'post',
                url:'https://upload-z1.qiniup.com',
                data:FD
             }).then(res=>{
                // console.log(res.data);
                this.$axios({
                    method:'post',
                    url:' https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/editUserInfo',
                    data:{
                        'uid':localStorage.getItem('uid'),
                        'avatar':`http://toutiao.longxiaokj.com/${res.data.key}`,
                        'nickname':this.valuename,
                        'birthday':this.time,
                        'sex':this.valuesex
                    }
                }).then(res=>{
                    Toast(res.data.msg)
                    // console.log("user",res);
                })

             })
        })
        
    },
    userbaocun(){
        this.chengeuser()
    },
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
    //   console.log(file);
      this.files=file.file
    //   console.log(this.files);
      this.headImg = file.content
    },
   },
   mounted(){
     this.userval=this.$route.params.userval
     this.valuename=this.userval.nickname
     this.time=this.userval.birthday
     this.valuesex=this.userval.sex
     this.headImg=this.userval.avatar
    //    console.log('2',this.userval);
   }
   
}
</script>

<style lang="less" scoped>
.msg_heard{
    display: flex;
    height: 45px;
    background-color: rgb(16, 173, 81);
    // line-height: 45px;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    .p{
        color: #fff;
        text-align: center;
    }
}
.btn{
    width: 350px;
    margin: 25px auto;
    // text-align: center;
}
.chengename{
    p{  
        margin-top: 10px;
        margin-left: 30px;
    }
    input{
        width: 80%;
        height: 30px;
        margin: 10px 0px 10px 30px;
    }
    .btn{
        display: flex;
        justify-content: space-around;
        align-items: center;
    }

}
.shijian{
    width: 80vw;
}
.box{
    .van-cell__value{
        font-size: 16px;
        font-weight: bold;
        color: #000;
    }
    .van-cell{
        align-items: center;
    }
}

</style>